<template>
	<v-app>
		<v-content>
			<v-overlay color="grey lighten-4" opacity="1">
				<v-card color="white" light elevation="6" width="500">
					<v-card-title>
						Login Unit
					</v-card-title>
					<v-form v-model="valid" ref="form">
						<v-card-text>
							<v-text-field
								outlined
								dense
								v-model="dataLogin.nohp"
								label="No Handphone"
								required
								:rules="requiredRule"
							></v-text-field>
							<v-text-field
								outlined
								dense
								v-model="dataLogin.password"
								label="Password"
								required
								:rules="requiredRule"
								:append-icon="showpassword ? 'mdi-eye' : 'mdi-eye-off'"
					      :type="showpassword ? 'text' : 'password'"
					      @click:append="showpassword = !showpassword"
							></v-text-field>

							<Notif :msg="msg" :status="status" @visible="visible" />
						</v-card-text>
						<v-card-actions>
							<v-spacer></v-spacer>
							<v-btn
								:disabled="!valid"
								type="submit"
								:loading="isLoading"
								@click.prevent="validate"
								depressed
								color="primary">
								login
							</v-btn>
						</v-card-actions>
					</v-form>
				</v-card>
			</v-overlay>
		</v-content>
	</v-app>
</template>

<script>
	import { postDataLogin } from '@/config/login'
	import Notif from '@/components/Notif'

	export default{
		data: () => ({
			valid: true,
			dataLogin: {
				nohp: '',
				password: ''
			},
			requiredRule: [v => !!v || 'Data harus di isi'],
			showpassword: false,
			status: null,
			isLoading: false,
			msg: {
				success: 'Login berhasil',
				error: 'Login gagal',
				visible: false
			}
		}),

		components: { Notif },

		methods: {
			validate() {
				if (this.$refs.form.validate()) {
					this.isLoading = true			
					postDataLogin(this.dataLogin)
						.then(res => {
							if (res.status === 200) {
								this.isLoading = false
								this.status = true
								window.localStorage.setItem('unit_id', res.data.data.id)
								window.localStorage.setItem('token_unit', res.data.data.access_token)
								window.location.href = '/unit'
								// console.log(res.data.data)
							}
						})
						.catch(err => {
							this.isLoading = false
							this.status = false
						})
					this.msg.visible = true
				}
			},
			visible(val) {
				this.msg.visible = val
			}
		}
	}
</script>
